.container {
  position: relative;
  width: 100%;
  margin-bottom: 2rem;
}

.image {
  display: block;
  padding-bottom: 1rem;
  width: 100%;
  height: 300px;
}
.myCard:hover{
  filter: grayscale(100%);
}
.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: black;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
  opacity: 0.7;
}

.container:hover .overlay {
  height: 30%;
}

h2{
  font-family: "Amatic SC";
  font-weight : 600; 
  color:white;
  margin-bottom:2rem;
  padding-top:2rem;
}

.text {
  width: 100%;
  padding: 10px 45px 10px 0;
  color: white;
  font-size: 12px;
  position: absolute;
  top: 50%;
  left: 65%;
  transform: translate(-60%, -50%);
  text-align: justify;
}
    